import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

const API_URL = 'https://server.gold-ticket.uz/api';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        agreed: null,
        lang: null,
        name: null,
        phone: null,
        photo: null,
        premium: null,
        username: null,
        tickets: null,
        tickets_count: null,
    },
    getters: {},
    mutations: {
        setUser(state, data){
            state.agreed = data.agreed;
            state.lang = data.lang;
            state.name = data.name;
            state.phone = data.phone;
            state.photo = data.photo;
            state.premium = data.premium;
            state.username = data.username;
        },
        setTickets(state, data){
            state.tickets = data.tickets_data;
            state.tickets_count = data.tickets_total;
        },
    },
    actions: {
        async getUser({ commit }, user_id) {
            try {
                const res = await axios.post(`${API_URL}/user/1821252109`)
                commit("setUser", res.data);
            } catch (e) {
                commit("error", e.response.data);
                throw e;
            }
        },
        async updateUser() {},
        async getTickets() {},
    },
})
